body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

li {
  list-style: none;
}
.shopeeLogo {
  height: 36px;
  fill: #f53d2d;
}
.logo {
  margin-right: 8px;
  height: 20px;
}

.title {
  color: #f53d2d;
}

.main {
  font-size: 24px;
}
.selectItemName {
  margin-left: 6px;
  color: #f53d2d;
}
.main-grid-container {
  display: grid;
  grid-template-columns: auto auto;
  grid-gap: 10px;
}
.view {
  background-color: rgba(255, 255, 255, 0.8);
  display: grid;
  grid-gap: 26px;
  grid-template-columns: auto;
}
.view canvas {
  margin-left: -80px;
}
.treeImg {
  grid-row-start: 1;
  grid-row-end: 3;
  text-align: center;
  overflow: scroll;
}
.displayTitle {
  display: flex;
  align-items: center;
  font-weight: 700;
  font-size: 18px;
  margin-top: 16px;
  margin-bottom: 6px;
}
h4 {
  margin-top: 6px;
  margin-bottom: 8px;
  font-weight: 700;
  color: #414141;
}
.elementDisplay {
  position: absolute;
  z-index: 9999;
  right: 0;
  top: 0;
  word-wrap: break-word;
  word-break: break-all;
  padding: 0 16px 16px;
  background-color: #bedef8;
}
.sepline {
  margin-top: 16px;
  border-top: 1px dashed rgb(61, 61, 61);
}
.propsDisplay {
  margin-top: 6px;
}
.propsDisplay span {
  color: #f53d2d;
}
area {
  cursor: pointer;
}

.switch {
  position: absolute;
  top: 20px;
  right: 10px;
  display: inline-block;
  width: 60px;
  height: 34px;
}

.switch input {
  opacity: 0;
  width: 0;
  height: 0;
}

.slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #ccc;
  -webkit-transition: 0.4s;
  transition: 0.4s;
}

.slider:before {
  position: absolute;
  content: "";
  height: 26px;
  width: 26px;
  left: 4px;
  bottom: 4px;
  background-color: white;
  -webkit-transition: 0.4s;
  transition: 0.4s;
}

input:checked + .slider {
  background-color: #2196f3;
}

input:focus + .slider {
  box-shadow: 0 0 1px #2196f3;
}

input:checked + .slider:before {
  -webkit-transform: translateX(26px);
  -ms-transform: translateX(26px);
  transform: translateX(26px);
}

/* Rounded sliders */
.slider.round {
  border-radius: 34px;
}

.slider.round:before {
  border-radius: 50%;
}
